import '../styles/index.scss';
import BeatMap from './beatmap';
import RhythmGame, { APPROACH_SECONDS } from './rhythmgame';
import Visualizer from './visualizer';
import Wavifier from './wavifier';
var stdev = require('standarddeviation');
var average = require('average');
var Uint8Array = window.Uint8Array;
if (process.env.NODE_ENV === 'development') {
    require('../index.html');
}
window.onload = function () {
    var audioFile = document.getElementById('audio-file');
    var osuDownloadButton = document.getElementById('osu_download');
    var utctfDownloadButton = document.getElementById('utctf_download');
    osuDownloadButton.style.display = "none";
    utctfDownloadButton.style.display = "none";
    var audioContext = new AudioContext();
    var audioBeginTime = 0;
    var sliderStreak = 10;
    audioFile.onchange = function () {
        audioFile.style.display = "none";
        var fileReader = new FileReader();
        var beatMap = new BeatMap(this.files[0].name);
        osuDownloadButton.onclick = function () {
            beatMap.downloadFile();
        };
        utctfDownloadButton.onclick = function () {
            beatMap.downloadUTCTFFile();
        };
        fileReader.readAsArrayBuffer(this.files[0]);
        fileReader.onload = function () {
            var arrayBuffer = this.result;
            audioContext.decodeAudioData(arrayBuffer, function (buffer) {
                var source = audioContext.createBufferSource();
                source.buffer = buffer;
                var filter = new BiquadFilterNode(audioContext, {
                    type: 'lowpass',
                    frequency: 100,
                });
                var analyzer = audioContext.createAnalyser();
                filter.connect(analyzer);
                analyzer.fftSize = 2048;
                var bufferLength = analyzer.frequencyBinCount;
                var frequencyBins = 64; // we care about the lowermost frequency bins
                var dataArray = new Uint8Array(bufferLength);
                var previousDataArray = new Uint8Array(bufferLength);
                var diffs = [];
                var currentlyOnBeat = false;
                var eachFrame = function () {
                    analyzer.getByteFrequencyData(dataArray);
                    var diff = 0;
                    var sliderShouldBeActive = false;
                    var sliderThreshold = Number.parseInt(document.getElementById('slider_threshold').value);
                    var sliderStickiness = Number.parseInt(document.getElementById('slider_stickiness').value);
                    var timestamp = audioContext.currentTime - audioBeginTime;
                    for (var i = 0; i < frequencyBins; i++) {
                        diff += Math.abs(dataArray[i] - previousDataArray[i]);
                        if (dataArray[i] > sliderThreshold) {
                            sliderShouldBeActive = true;
                        }
                    }
                    if (!beatMap.sliderIsActive() && sliderShouldBeActive) {
                        sliderStreak++;
                        if (sliderStreak >= sliderStickiness) {
                            beatMap.beginSlider(timestamp);
                            sliderStreak = 0;
                        }
                    }
                    if (beatMap.sliderIsActive() && !sliderShouldBeActive) {
                        sliderStreak++;
                        if (sliderStreak >= sliderStickiness) {
                            beatMap.endSlider();
                            sliderStreak = 0;
                        }
                    }
                    diffs.push(diff);
                    var avg = average(diffs);
                    var std = stdev.calculateStandardDeviation(diffs);
                    var sensitivity = 2 - Number.parseFloat(document.getElementById("sensitivity").value);
                    if (diff > avg + std * sensitivity) {
                        if (!currentlyOnBeat) {
                            beatMap.addBeat(timestamp);
                        }
                        currentlyOnBeat = true;
                    }
                    else {
                        currentlyOnBeat = false;
                    }
                    var rollingDiffsSize = Number.parseInt(document.getElementById("sustain").value);
                    while (diffs.length > rollingDiffsSize) {
                        diffs.shift();
                    }
                    previousDataArray.set(dataArray);
                };
                setInterval(eachFrame, 30);
                source.connect(filter);
                var visualizerAnalyzer = audioContext.createAnalyser();
                visualizerAnalyzer.fftSize = 32;
                source
                    .connect(new DelayNode(audioContext, {
                    "delayTime": APPROACH_SECONDS
                }))
                    .connect(visualizerAnalyzer)
                    .connect(audioContext.destination);
                new Visualizer(document.getElementById("visualizer"), visualizerAnalyzer);
                new Wavifier(document.getElementById("visualizer"));
                source.start();
                audioBeginTime = audioContext.currentTime;
                new RhythmGame(document.getElementById('game'), beatMap, audioBeginTime, audioContext);
                source.addEventListener('ended', function () {
                    osuDownloadButton.style.display = "inline";
                    utctfDownloadButton.style.display = "inline";
                    console.log('done');
                });
            });
        };
    };
};
