import Note from "./note";
var BEATMAP_HEADER = "osu file format v14\n\n[General]\nAudioFilename: ";
//The rest of the file data is stored here temporarily, will make customizable at a later date
var TEMP_DATA = "\nMode: 3\n\n[Editor]\n\n[Metadata]\nTitle:Song\nTitleUnicode:Song\nArtist:\nArtistUnicode:\nCreator:Andrew\nVersion:Andrew's Easy\nSource:\nTags:\nBeatmapID:1\nBeatmapSetID:1\n\n[Difficulty]\nHPDrainRate:6.5\nCircleSize:4\nOverallDifficulty:6.5\nApproachRate:5\nSliderMultiplier:1.4\nSliderTickRate:1\n\n[Events]\n\n[TimingPoints]\n//second number is bpm - 1 / value * 1000 * 60 = bpm, 333.33 = 180 bpm\n0,333.33,4,0,0,40,1,0\n\n[HitObjects]\n";
var BeatMap = /** @class */ (function () {
    function BeatMap(filename) {
        var _this = this;
        this.activeSlider = -1;
        this.notes = [];
        this.sliders = [];
        this.filename = filename;
        setInterval(function () {
            if (_this.activeSlider != -1) {
                _this.sliders.push(new Note(_this.sliderSongStartTime + (performance.now() - _this.sliderStartTime) / 1000, _this.activeSlider));
                console.log("add new slider");
            }
        }, 50);
    }
    BeatMap.prototype.addBeat = function (time) {
        var buttonId = Math.floor(Math.random() * 4);
        while (buttonId == this.activeSlider) {
            buttonId = Math.floor(Math.random() * 4);
        }
        this.notes.push(new Note(time, buttonId));
    };
    BeatMap.prototype.beginSlider = function (time) {
        console.log("Begin");
        this.sliderSongStartTime = time;
        this.sliderStartTime = performance.now();
        this.activeSlider = Math.floor(Math.random() * 4);
    };
    BeatMap.prototype.endSlider = function () {
        console.log("End");
        this.activeSlider = -1;
    };
    BeatMap.prototype.sliderIsActive = function () {
        return this.activeSlider != -1;
    };
    BeatMap.prototype.downloadUTCTFFile = function () {
        var bytes = [];
        this.notes.forEach(function (note) {
            bytes.push.apply(bytes, note.toBytes());
        });
        var arrayBuffer = new ArrayBuffer(bytes.length);
        var view = new Uint8Array(arrayBuffer);
        for (var i = 0; i < bytes.length; i++) {
            view[i] = bytes[i];
        }
        var blob = new Blob([arrayBuffer], { type: 'application/octet-stream' });
        var link = document.createElement('a');
        link.href = URL.createObjectURL(blob);
        link.download = 'beatmap.map';
        link.click();
    };
    BeatMap.prototype.downloadFile = function () {
        var notestr = "";
        this.notes.forEach(function (note) {
            notestr += note.toString();
        });
        var blob = new Blob([BEATMAP_HEADER, this.filename, TEMP_DATA, notestr], { type: 'application/octet-stream' });
        var link = document.createElement('a');
        link.href = URL.createObjectURL(blob);
        link.download = 'beatmap.osu';
        link.click();
    };
    return BeatMap;
}());
export default BeatMap;
